.slider__list {
  margin: 0 auto;
  margin-bottom: 80px;
  padding: 0;

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.slider__item {
  display: none;
}

.slider__item--active {
  display: flex;
  flex-direction: column;
}

.slider__toggles {
  display: flex;
  position: absolute;
  width: 88px;
  height: 18px;
  list-style: none;
  margin: 0 auto;
  padding: 0;
}

.slider__toggle {
  margin-right: 16px;
  width: 10px;
  height: 10px;

  &:last-child {
    margin-right: 0;
  }
}

.slider__toggle label {
  display: block;
  position: relative;
  cursor: pointer;
  user-select: none;
  width: 6px;
  height: 6px;
  background: #bfc3c7;
  border-radius: 50%;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.1;
  }
}

.slider__toggle input {
  display: none;
}

.slider__toggle input:checked + label::after {
  position: absolute;
  top: -4px;
  left: -4px;
  content: "";
  width: 14px;
  height: 14px;
  background: #ffffff;
  border: 3px solid #283645;
  opacity: 1;
  border-radius: 50%;
  cursor: pointer;
}
